.TickersList__list{padding:0;display:grid;justify-content:center;grid-template-columns:repeat(2, 120px);gap:15px;margin-top:50px}@media(min-width: 450px){.TickersList__list{grid-template-columns:repeat(3, 150px)}}@media(min-width: 800px){.TickersList__list{display:flex;flex-direction:row;justify-content:space-around}}.TickersList__item{list-style:none;display:flex;flex-direction:column;font-weight:500;text-align:center;padding:5px 30px;border-radius:5px;border:1px solid #e4f1e9;transition:.3s;cursor:pointer}.TickersList__item::after{content:"add";color:#576157}.TickersList__item:hover{background-color:#e4f1e9;transition:.3s}.TickersList__item--selected{background-color:#e4f1e9}.TickersList__item--selected::after{content:"✓";color:green}.TickersList__table{table-layout:fixed;width:100%;border:5px solid #fff;border-radius:3px;border-collapse:collapse;outline:3px solid #01389d;margin-top:50px}.TickersList__table-header{font-family:Arial,Helvetica,sans-serif;font-size:12px;letter-spacing:2px;background-color:#01389d;color:#fff;padding:15px}.TickersList__table-header:first-child{width:3%}.TickersList__table-header:nth-child(7){width:8%}.TickersList__table-header:nth-child(8){width:12%}.TickersList__table-header:last-child{width:2%}@media(max-width: 698px){.TickersList__table-header--none{display:none}}.TickersList__message{text-align:center;font-size:20px;padding:20px}.TickersList--AAPL{background-color:#666;padding:5px 10px;border-radius:3px}.TickersList--GOOGL{background-color:#469c46;padding:5px 10px;border-radius:3px}.TickersList--MSFT{background-color:#d5ec61;padding:5px 10px;border-radius:3px}.TickersList--AMZN{background-color:#c26c03;padding:5px 10px;border-radius:3px}.TickersList--FB{background-color:#711dc4;padding:5px 10px;border-radius:3px}.TickersList--TSLA{background-color:#e31937;padding:5px 10px;border-radius:3px}